.container{
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.container label{
    font-weight: 700;
    font-size: 2rem;
    color: #4b4b4b;
}

.container input{
    border-radius: 5px;
    border: 2px solid #eee;
    outline: none;
    padding: 10px 15px;
    color: #000;
}

.container input:hover{
    border-color: #ccc;
}

.container input:focus{
    border-color: #9900ff;
}