.container {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    background: linear-gradient(to right, #e100ff, #808bff);
    display: flex;
    justify-content: center;
    align-items: center;
}

.innerBox {
    min-width: 500px;
    height: fit-content;
    width: fit-content;
    background-color: whitesmoke;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
    padding: 30px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 30px;

}

.footer {
    display: flex;
    flex-direction: column;
    gap: 20px;
}



.footer button {
    outline: none;
    border: none;
    background-color: #9900ff;
    color: #fff;
    border-radius: 5px;
    font-weight: bold;
    font-size: 2rem;
    padding: 10px 16px;
    width: 100%;
    transition: 100ms;
    cursor: pointer;
}

.footer button:disabled {
    background-color: gray !important;
}

.footer button:hover {
    background-color: #aa2aff;
}

.footer p {
    font-weight: 700;
    color: #000;
}

.footer p span {
    font-weight: bold;
    color: #9900ff;
    letter-spacing: 1px;
    font-size: 2rem;
    text-decoration: none;
}



.heading {
    font-family: 'Courier New', Courier, monospace;
    font-size: 7rem;
    font-weight: 800;
}